#intro{
    height: cal(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    
}

.bg{
    position: absolute;
    top: -4rem;
    z-index: -1;
    right: 0;
    object-fit: cover;
    height: 100vh;

}

.introContent{
    height: 100vh;
    width: 100;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: black;

}

.hello{
    font-size: 1.75rem;
    font-weight: 100;
    color: black;
}

.introName{
    /* color: yellow; */
    color: rgb(255, 44, 90);

}

.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
    color: black;
}

.btn{
    /* background: white; */
    background: rgb(255, 44, 90);
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}


.btnImg{
    object-fit: cover;
    margin: 0  0.25rem;
    height: 1rem;
}

@media screen and (max-width:480px){
    .bg{
        right: -20vw;
    }

}